<template>
  <div class="register">
    <!--导航内容开始-->
    <header>
      <div class="navigation container clearfix">
        <div class="pull-left">
          <router-link class="logo" to="/"><img src="static/images/index/logo_02.png" alt="" /></router-link>
          <span>{{ $t('register.nav.title') }}</span>
        </div>
        <div class="menu2 pull-right">
          {{ $t('register.des.part1') }}<router-link to="/login" class="menu2-login">{{ $t('register.des.part2') }}</router-link><span class="icon-back"></span>&nbsp;<router-link to="/" class="back-index">{{ $t('register.des.part3') }}</router-link>
        </div>
      </div>
    </header>
		<!--导航内容结束-->

    <div class="form">
      <div class="title"><span :class="{'c-violet': tab.phone}" @click="tab.phone = true; tab.email = false">{{ $t('register.regWays.byPhone') }}</span><span :class="{'c-violet': tab.email}" @click="tab.phone = false; tab.email = true">{{ $t('register.regWays.buEmail') }}</span></div>
      <el-form :model="ruleForm" :status-icon="true" :rules="rules" ref="ruleForm" label-width="0" class="demo-ruleForm" v-show="tab.phone" key="1">
        <el-form-item class="phone-account" prop="user_account">
          <el-select v-model="ruleForm.code_area" class="phone-code-area" popper-class="option-list">
            <el-input
              prefix-icon="el-icon-search"
              v-model="searchContent" class="search-content" @input="searchInputChange">
            </el-input>
            <el-option :value="'+' + item.code_area" v-for="item in smsCodeList" :key="item.code_id">
              <div class="clearfix code-area"><div class="pull-left area">{{ item.nation_cn }}</div><div class="pull-right">+{{ item.code_area }}</div></div>
            </el-option>
          </el-select>
          <el-input v-model.trim="ruleForm.user_account" :placeholder="$t('register.placeholder.tel')" class="phone-num"></el-input>
          <p class="tips clearfix"><img src="static/images/login_register/tips.jpg"/>{{ $t('register.tips.phone') }}</p>
        </el-form-item>
        <el-form-item prop="sms_verify" class="verify">
          <el-input type="text" auto-complete="off" :placeholder="$t('register.placeholder.verifyCode')" v-model.trim="ruleForm.sms_verify"></el-input><el-button class="get-verify" @click="sendRequestSms($event)" :disabled="!validCode">{{ validTip }}</el-button>
        </el-form-item>
        <el-form-item  prop="user_password">
          <el-input type="password" v-model.trim="ruleForm.user_password"  :placeholder="$t('register.placeholder.pwd')"></el-input>
          <p class="tips clearfix"><img src="static/images/login_register/tips.jpg"/>{{ $t('register.tips.pwd') }}</p>
        </el-form-item>
        <el-form-item  prop="user_confirmpwd">
          <el-input type="password" v-model.trim="ruleForm.user_confirmpwd"  :placeholder="$t('register.placeholder.confirmPwd')"></el-input>
        </el-form-item>
        <el-form-item prop="invite_code">
          <el-input v-model.trim="ruleForm.invite_code" :placeholder="$t('register.placeholder.inviteCode')"></el-input>
        </el-form-item>
        <el-form-item class="proto" prop="proto">
          <el-checkbox-group v-model="ruleForm.proto">
            <el-checkbox label="" name="type">{{ $t('register.proto.part1') }}<router-link to="/proto">{{ $t('register.proto.part2') }}</router-link></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')" v-text="$t('register.title')">注册</el-button>
        </el-form-item>
      </el-form>

      <el-form :model="ruleForm1" :status-icon="true" :rules="rules1" ref="ruleForm1" label-width="0" class="demo-ruleForm" v-show="tab.email" key="2">
        <el-form-item prop="user_account" >
          <el-input v-model.trim="ruleForm1.user_account" :placeholder="$t('register.placeholder.email')"></el-input>
          <p class="tips clearfix"><img src="static/images/login_register/tips.jpg"/>{{ $t('register.tips.email') }}</p>
        </el-form-item>
        <el-form-item prop="sms_verify" class="verify">
          <el-input type="text" auto-complete="off" :placeholder="$t('register.placeholder.verifyCode')" v-model.trim="ruleForm1.sms_verify"></el-input><el-button class="get-verify" @click="sendRequestSms($event)" :disabled="!validCode1">{{ validTip1 }}</el-button>
        </el-form-item>
        <el-form-item  prop="user_password">
          <el-input type="password" v-model.trim="ruleForm1.user_password"  :placeholder="$t('register.placeholder.pwd')"></el-input>
          <p class="tips clearfix"><img src="static/images/login_register/tips.jpg"/>{{ $t('register.tips.pwd') }}</p>
        </el-form-item>
        <el-form-item  prop="user_confirmpwd">
          <el-input type="password" v-model.trim="ruleForm1.user_confirmpwd"  :placeholder="$t('register.placeholder.confirmPwd')"></el-input>
        </el-form-item>
        <el-form-item prop="invite_code">
          <el-input v-model.trim="ruleForm1.invite_code" :placeholder="$t('register.placeholder.inviteCode')"></el-input>
        </el-form-item>
        <el-form-item class="proto" prop="proto1">
          <el-checkbox-group v-model="ruleForm1.proto1">
            <el-checkbox label="" name="type">{{ $t('register.proto.part1') }}<router-link to="/proto">{{ $t('register.proto.part2') }}</router-link></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm1')" v-text="$t('register.title')">注册</el-button>
        </el-form-item>
      </el-form>
    </div>
    
    <footer>
      <div class="">
        {{ $t('register.footer') }}
      </div>
    </footer>
  </div>
</template>
<script>
  import { setToken } from '@/utils/auth'
  import { loginPwd, Email, nationNumber } from '@/utils/validate'
  import { submitRegister, sendRequestSms, login, getInternationalSmsCode } from '@/api/loginModule'
  import md5 from 'js-md5'
  // import { getmyAdList, openORclose, delAd } from '@/api/orderManage'
  export default {
    data() {
      const validatePassword = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(this.$t('errorTips.withoutPwd')))
        } else if (value.length < 6) {
          callback(new Error(this.$t('errorTips.longPwd')))
        } else if (value.length > 20) {
          callback(new Error(this.$t('errorTips.longLimitPwd')))
        } else if (!loginPwd(value)) {
          callback(new Error(this.$t('errorTips.rulePwd')))
        } else {
          callback()
        }
      }
      const validateUserName = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(this.$t('errorTips.withoutEmail')))
        } else {
          if (Email(value)) {
            callback()
          } else {
            callback(new Error(this.$t('errorTips.emailFormat')))
          }
        }
      }
      const validateNumber = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(this.$t('errorTips.withoutPhone')))
        } else {
          if (nationNumber(value)) {
            callback()
          } else {
            callback(new Error(this.$t('errorTips.phoneFormat')))
          }
        }
      }
      const validatePass2 = (rule, value, callback) => {
        let password
        if (this.tab.phone) {
          password = this.ruleForm.user_password
        } else {
          password = this.ruleForm1.user_password
        }
  
        if (value === '') {
          callback(new Error(this.$t('errorTips.surePwd')))
        } else if (value !== password) {
          callback(new Error(this.$t('errorTips.surePwdError')))
        } else {
          callback()
        }
      }
      return {
        tab: {
          phone: true,
          email: false
        },
        smsCodeList: [],
        searchContent: '',
        ruleForm: {
          code_area: '',
          user_account: '',
          user_password: '',
          sms_verify: '',
          user_confirmpwd: '',
          invite_code: '',
          proto: []
        },
        ruleForm1: {
          user_account: '',
          user_password: '',
          sms_verify: '',
          user_confirmpwd: '',
          invite_code: '',
          proto1: []
        },
        rules: {
          user_account: [
            { validator: validateNumber, trigger: 'blur' }
          ],
          user_password: [
            { validator: validatePassword, trigger: 'blur' }
          ],
          sms_verify: [
            { required: true, message: this.$t('errorTips.withoutVerifyCode'), trigger: 'blur' }
          ],
          user_confirmpwd: [
            { validator: validatePass2, trigger: 'blur' },
            { required: true, message: this.$t('errorTips.withoutSurePwd'), trigger: 'blur' }
          ],
          proto: [
            { type: 'array', required: true, message: this.$t('errorTips.withoutProto'), trigger: 'change' }
          ]
        },
        rules1: {
          user_account: [
            { validator: validateUserName, trigger: 'blur' }
          ],
          user_password: [
            { validator: validatePassword, trigger: 'blur' }
          ],
          sms_verify: [
            { required: true, message: this.$t('errorTips.withoutVerifyCode'), trigger: 'blur' }
          ],
          user_confirmpwd: [
            { validator: validatePass2, trigger: 'blur' },
            { required: true, message: this.$t('errorTips.withoutSurePwd'), trigger: 'blur' }
          ],
          proto1: [
            { type: 'array', required: true, message: this.$t('errorTips.withoutProto'), trigger: 'change' }
          ]
        },
        validCode: true,
        validState: true,
        validTip: this.$t('verifyCode.title'),
        validCode1: true,
        validState1: true,
        validTip1: this.$t('verifyCode.title')
      }
    },
    mounted() {
      document.body.className = 'bgWhite'
      this.handleGetInternationalSmsCode()
      if (this.$route.query.invite_code) {
        this.ruleForm.invite_code = this.$route.query.invite_code
        this.ruleForm1.invite_code = this.$route.query.invite_code
      }
    },
    beforeRouteLeave(to, from, next) {
      document.body.className = ''
      next()
    },
    methods: {
      searchInputChange() {
        if (this.searchContent === '') {
          this.smsCodeList = this.smsCodeListCopy
          return false
        }
        const newList = []
        this.smsCodeList.forEach(element => {
          const reg = new RegExp(this.searchContent)
          if (reg.test(element.nation_cn)) {
            newList.push(element)
          }
        })
        if (newList.length !== 0) {
          this.smsCodeList = [...newList]
        } else {
          this.smsCodeList = this.smsCodeListCopy
        }
      },
      handleGetInternationalSmsCode() {
        getInternationalSmsCode().then(res => {
          console.log('res', res)
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
  
          this.smsCodeList = [...res.data]
  
          // this.smsCodeList.unshift(res.data[112])
          // this.smsCodeList.unshift(res.data[84])
          this.smsCodeList.unshift(res.data[res.data.length - 1])
          this.smsCodeList.pop()
          this.smsCodeListCopy = [...this.smsCodeList]
          // const length = res.data.length - 1
          this.ruleForm.code_area = '+' + this.smsCodeList[0].code_area
        }).catch(() => {
        })
      },
      submitForm(formName) {
        setTimeout(function() {

        }, 1)
        this.$refs[formName].validate((valid) => {
          console.log('验证---')
          if (valid) {
            console.log(this[formName])
            const data = { ...this[formName] }
            if (this[formName].code_area) {
              data.code_area = this[formName].code_area.substring(1)
            }
            data.user_password = md5(this[formName].user_password)
            data.user_confirmpwd = md5(this[formName].user_password)
            const _this = this
            submitRegister(data).then(res => {
              console.log('res', res)
              if (res.status !== 1000) {
                _this.$message.error(res.msg)
                return false
              }
              console.log('注册成功')
              _this.handleLogin(formName)
            }).catch(() => {
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      formatName(val) {
        if (!val || val.length <= 4) return val
        const result = val.substr(0, 3) + '***' + val.substr(-4, 4)
        return result
      },
      handleLogin(formName) {
        console.log('enter')
        const data = {}
        data.user_account = this[formName].user_account
        data.user_password = md5(this[formName].user_password)
        console.log(data)
        login(data).then(res => {
          console.log('res', res)
          if (res.status !== 1000) {
            this.$message.error(res.msg)
          } else {
            console.log('登录成功')
            setToken(res.data.token)
            const userInfo = JSON.stringify({
              'uid': res.data.id,
              'token': res.data.token,
              'user_account': res.data.nickname, // 登录帐号
              'nickname': res.data.nickname,
              'head': res.data.head,
              'email': res.data.email,
              'phone': res.data.phone
            })
            window.localStorage.setItem('link_userInfo', userInfo)
            console.log('将账户信息缓存到本地')
            const userinfoObj = JSON.parse(userInfo)
            this.$store.commit('SET_USER_AVATAR', userinfoObj.head)
            if (userinfoObj.nickname) this.$store.commit('SET_USER_NICKNAME', userinfoObj.nickname)
            if (userinfoObj.phone) this.$store.commit('SET_USER_PHONE', this.formatName(userinfoObj.phone))
            if (userinfoObj.email) this.$store.commit('SET_USER_EMAIL', this.formatName(userinfoObj.email))
            this.$router.push('/registerSuccess')
          }
        }).catch(() => {
        })
      },
      handlePhoneState() {
        if (!this.ruleForm.user_account) {
          this.$message.error(this.$t('errorTips.withoutPhone'))
          return false
        } else {
          if (!nationNumber(this.ruleForm.user_account)) {
            this.$message.error(this.$t('errorTips.phoneFormat'))
            return false
          }
        }
        return true
      },
      handleEmailState() {
        if (!this.ruleForm1.user_account) {
          this.$message.error(this.$t('errorTips.withoutEmail'))
          return false
        } else {
          if (!Email(this.ruleForm1.user_account)) {
            this.$message.error(this.$t('errorTips.emailFormat'))
            return false
          }
        }
        return true
      },
      sendRequestSms(event) {
        let flag = false
        const data = {}
        let state = ''
        if (this.tab.phone) {
          state = 'phone'
          flag = this.handlePhoneState()
          data.user_account = this.ruleForm.user_account
          data.code_area = this.ruleForm.code_area.substring(1)
        } else {
          state = 'email'
          flag = this.handleEmailState()
          data.user_account = this.ruleForm1.user_account
        }
  
        data.request_type = 'register'
        if (flag) {
          sendRequestSms(data).then(res => {
            console.log('res', res)
            if (res.status !== 1000) {
              this.$message.error(res.msg)
              return false
            }
  
            this.setTime(state)
          }).catch(() => {
          })
        }
      },
      setTime(str) {
        if (str === 'phone') {
          let time = 60
          if (this.validCode) {
            this.validCode = false
            const t = setInterval(() => {
              time--
              this.validTip = time + this.$t('verifyCode.second')
              if (time === 0) {
                clearInterval(t)
                this.validTip = this.$t('verifyCode.getAgain')
                this.validCode = true
                this.validState = true
              }
            }, 1000)
            return t
          }
        } else {
          let time = 60
          if (this.validCode1) {
            this.validCode1 = false
            const _this = this
            const t = setInterval(function() {
              time--
              _this.validTip1 = time + _this.$t('verifyCode.second')
              if (time === 0) {
                clearInterval(t)
                _this.validTip1 = _this.$t('verifyCode.getAgain')
                _this.validCode1 = true
                _this.validState1 = true
              }
            }, 1000)
            return t
          }
        }
      }
    }
  }
</script>


<style lang="less" scoped>
  .register{
    .phone-account{
      // overflow: hidden;
      /deep/.el-select .el-input__inner{
        width: 100px;
      }
      .area{
        width: 300px !important;
      }
      /deep/ .el-input--suffix .el-input__inner{
        height: 34px;
      }
      .phone-num{
        /deep/ .el-input__inner{
          padding-left: 120px;
        }
      }
      .phone-code-area{
        position: absolute;
        z-index: 4;
        top: 1px;
        left: 1px;
        border-right: 1px solid #dcdfe6;
        /deep/ .el-input__inner{
          border: none;
          left: 0;
        }
        /deep/ .el-input__validateIcon{
          display: none;
        }
      }
      
      /deep/ .el-input__inner:focus{
        border-color: #dcdfe6;
      }
      
    }
    .title{
      font-size: 14px;
      margin-bottom: 25px;
      span{
        margin-right: 30px;
        cursor: pointer;
      }
    }
    
    header {
      background: white;
      box-shadow: 0 1px 15px 0px #f3f3f4;
      width: 100%;
      height: 90px;
    }
    .navigation {
      color: white;
      line-height: 90px;
    }
    .navigation .menu2 {
      color: #2f3032;
      font-size: 12px;
    }
    .navigation .menu2 a {
      color: #2f3032;
    }
    .navigation .menu2 .menu2-login {
      font-size: 12px;
      color: rgba(31, 168, 246, 1);
      padding: 0 10px 0 7px;
    }
    .navigation > .pull-left > span {
      color: #2f3032;
      font-size: 18px;
      display: inline-block;
      margin-left: 20px;
      padding-left: 14px;
      border-left: 1px solid #c7cbd1;
      height: 20px;
      line-height: 20px;
      margin-top: 40px;
    }
    .logo img {
      margin: 13px 10px 24px 0;
      vertical-align: middle;
      border: 0;
    }
    .navigation .pull-left span{
      color: #2f3032;
      font-size: 18px;
      display: inline-block;
      margin-left: 20px;
      padding-left: 10px;
      border-left: 1px solid #c7cbd1;
      height: 20px;
      line-height: 20px;
      margin-top: 40px;
    }
    .form{
      width: 340px;
      margin: 0 auto;
      margin-top: 125px;
      .el-checkbox__label a{
        color: #1fa8f6;
      }
    }
    footer {
      text-align: center;
      color: #94969b;
      font-size: 12px;
      border-top: 1px solid #f0f0f4;
      padding-top: 35px;
      margin-top: 70px;
    }
    .tips {
      position: absolute;
      left: 350px;
      top: 0px;
      color: #d2d0d5;
      width: 500px;
      font-size: 12px;
      line-height: 40px;
      img{
        margin-right: 5px;
        margin-top: 12px;
        display: block;
        float: left;
      }
    }
    .el-button--primary{
      width: 100%;
    }
    /deep/ .verify .el-input__inner,
    .verify .el-input{
      width: 213px;
    }
    .get-verify {
      width: 110px;
      margin-left: 15px;
      display: inline-block;
      padding: 12px 13px;
      background: rgba(31, 168, 246, 0.15);
      color: rgba(31, 168, 246, .7);
    }
    /deep/ .el-checkbox__input.is-checked + .el-checkbox__label {
      color: #606266;
    }
    .proto{
      /deep/ .el-form-item__content{
        line-height: 20px;
        margin-top: -5px;
      }
    } 
 }
</style>

<style lang="less">
  .option-list{
    .el-select-dropdown__list{
      padding: 6px;
    }
    .code-area{
      width: 286px !important;
    }
    .el-input__suffix{
      display: none;
    }
  }
</style>


